            //randon
            jwt_secret_key: "",//includes Small Alpabet and 0-9
            ws_token: "",//include small capital,large alphaet and character and 0-9
            //to start bot
            strategy: ""
        }
    },
    methods: {
        //just like middleware
        dataValidation() {
            let validate = {
                msg: "",
                error: true
            }
            if (this.bot_name == "") {
                validate.msg = `Please Enter the "Botname"` ;
                validate.error = false
                this.$refs.BotNameFocus.focus();
                return validate;
            }
            if (this.exchange == "") {
                validate.msg = `Please Select the "Exchange Method"` ;
                validate.error = false
                this.$refs.ExchangeFocus.focus();
                return validate;
            }

            if (this.timeframe == "") {
                validate.msg = `Please Select the "Timeframe"` ;
                validate.error = false
                return validate;
            }
            if (this.stake_currency == "") {
                validate.msg = `Please Select the "Currency"` ;
                validate.error = false
                this.$refs.StakeCurrencyFocus.focus();

                return validate;
            }
            if (this.stake_amount <= 0) {
                validate.msg = `Please Enter the "Stake Amount"` ;
                validate.error = false
                this.$refs.stakeAmountFocuse.focus();
                return validate;
            }

            if (this.max_open_trades <= 0) {
                validate.msg = `Please Enter the "Max open trade"` ;
                validate.error = false
                this.$refs.maxOpenTradeFocus.focus();
                return validate;
            }
            if (this.strategy == "") {
                validate.msg = `Please Select the "Strategy"` ;
                validate.error = false
                this.$refs.StakeCurrencyFocus.focus();

                return validate;
            }
            return validate;
        },
        //small=15,cap=12,number 6 chaacter=1 total 34
        get_wss_token() {
            const capitalLetters = 'ABCDEFGHIJKLMNOPQRSTUVWXYZ';
            const smallLetters = 'abcdefghijklmnopqrstuvwxyz';
            const numbers = '0123456789';
            const specialChars = '!@#$%^&*()_+-={}[]|\\<>,.?/~`';

            let random_captital_letter = ""
            for (let i = 0; i < 12; i++) {
                random_captital_letter += capitalLetters.charAt(Math.floor(Math.random() * capitalLetters.length));
            }
            let random_small_letter = ""
            for (let i = 0; i < 15; i++) {
                random_small_letter += smallLetters.charAt(Math.floor(Math.random() * smallLetters.length));
            }
            let random_numbers = "";
            for (let i = 0; i < 6; i++) {
                random_numbers += numbers.charAt(Math.floor(Math.random() * numbers.length));
            }

            let random_sepcial_ch = specialChars.charAt(Math.floor(Math.random() * specialChars.length));
            random_sepcial_ch = specialChars.charAt(Math.floor(Math.random() * specialChars.length));
            let cmombination = random_captital_letter + random_small_letter + random_numbers + random_sepcial_ch;
            let combinationarray = cmombination.split('').sort(() => 0.5 - Math.random());
            let ws_token = combinationarray.join('');
            // console.log(ws_token)
            return ws_token;



        },
        //small 27 ,number37
        get_jwt_secret_key() {
            const smallLetters = 'abcdefghijklmnopqrstuvwxyz';
            const numbers = '0123456789';
            let random_small_letter = "";
            for (let i = 0; i < 27; i++) {
                random_small_letter += smallLetters.charAt(Math.floor(Math.random() * smallLetters.length))
            }
            // console.log(random_small_letter);
            let random_number = "";
            for (let i = 0; i < 37; i++) {
                random_number += numbers.charAt(Math.floor(Math.random() * numbers.length));
            }
            // console.log(random_number)
            let cmombination = random_small_letter + random_number;
            // console.log(cmombination)
            let combinationarray = cmombination.split('').sort(() => 0.5 - Math.random());
            // console.log(combinationarray)
            let jwt_secret_key = combinationarray.join('');
            // console.log(jwt_secret_key)
            return jwt_secret_key;



        },
        
        async create_bot() {
            const check_validation = this.dataValidation();

            if (check_validation.error) {
                try {

                const botCreatingData = {
                    "max_open_trades": this.max_open_trades,
                    "stake_currency": this.stake_currency,
                    "stake_amount": this.stake_amount,
                    "tradable_balance_ratio": 0.99,
                    "fiat_display_currency": "USD",
                    "timeframe": this.timeframe,
                    "dry_run": true,
                    "dry_run_wallet": 1000,
                    "cancel_open_orders_on_exit": false,
                    "trading_mode": "spot",
                    "margin_mode": "isolated",
                    "unfilledtimeout": {
                        "entry": 10,
                        "exit": 10,
                        "exit_timeout_count": 0,
                        "unit": "minutes"
                    },
                    "entry_pricing": {
                        "price_side": "same",
                        "use_order_book": true,
                        "order_book_top": 1,
                        "price_last_balance": 0.0,
                        "check_depth_of_market": {
                            "enabled": false,
                            "bids_to_ask_delta": 1
                        }
                    },
                    "exit_pricing": {
                        "price_side": "same",
                        "use_order_book": true,
                        "order_book_top": 1
                    },
                    "exchange": {
                        "name": this.exchange,
                        "key":this.key,
                        "secret": this.secret,
                        "ccxt_config": {},
                        "ccxt_async_config": {},
                        "pair_whitelist": [],
                        "pair_blacklist": ["BNB/.*"]
                    },
                    "pairlists": [{
                        "method": "VolumePairList",
                        "number_assets": 20,
                        "sort_key": "quoteVolume",
                        "min_value": 0,
                        "refresh_period": 1800
                    }],
                    "telegram": {
                        "enabled": false,
                        "token": "",
                        "chat_id": ""
                    },
                    "api_server": {
                        "enabled": true,
                        "listen_ip_address": "127.0.0.1",
                        "listen_port":8100,
                        "verbosity": "error",
                        "enable_openapi": false,
                        "jwt_secret_key": this.get_jwt_secret_key(),
                        "ws_token": this.get_wss_token(),
                        "CORS_origins": ["http://localhost:3000"],
                        "username": this.username,
                        "password": this.password
                    },
                        "bot_name": this.bot_name,
                        "user_data_dir": "/home/malik/Desktop/freqtrade/user_data/",
                        "strategy_path": "/home/malik/Desktop/freqtrade/user_data/",
                        "strategy": this.strategy,
                        "db_url": `sqlite://///home/malik/Desktop/freqtrade/user_data/${this.bot_name}.sqlite`,
                        "logfile": "syslog:/dev/log",
                        "initial_state": "running",
                        "force_entry_enable": false,
                        "internals": {
                            "process_throttle_secs": 5
                        }
                };

                const response = await axios.post('http://127.0.0.1:8000/create/', JSON.stringify(botCreatingData));
                console.log('Registration successful!', response);
                if (response.status === 200) {
                    alert(`"${botCreatingData.bot_name}" Bot created successfully!`);
                    //going to parent componenet
                    this.$router.go(-1);
                    //clear the values
                    this.bot_name="";
                    this.exchange="";
                    this.timeframe="";
                    this.stake_currency="";
                    this.stake_amount=0;
                    this.max_open_trades=0;
                    this.strategy="";
                }
                else {
                    alert("Bot is not created yet")
                }

                } catch (error) {
                console.error('Registration failed!', error);
                }
            }
            else {
                alert(check_validation.msg)
            }

        }

        // console.log(this.bot_name);
        // console.log(this.username);


        // console.log(this.bot_name);
        // console.log(this.username);
        // console.log(this.password);
        // console.log(this.max_open_trades);
        // console.log(this.timeframe)
        // console.log(this.stake_currency)
        // console.log(this.stake_amount)
    },


